
.new_app {
  margin: 45px;
  width: 100%;
  background-color: white;
}
.new_app_header{
  width: 100%;
  display: inline;
}
.new_app_header label{
  background-color: #2e73bc;
  color: white;
  border: solid #2e73bc 1px ;
  border-radius: 3px;
  float: right;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 8px;
  padding-top: 3px;
  text-align: center;
}
.new_app_header label:hover{
  cursor: pointer;
}
.new_app_header label span{
  font-weight: 900;
  font-size: 25px;
}
.applications{
  margin-top: 50px;
}
.ssh,.generate input{
    text-align: center;
}



